.cont3{
    width:100%;
    /* height:150vh; */
    background:black;
    background: transparent;
    overflow: hidden;
    display: flex;
    flex-direction: column;

}

.c-li-1{
    width:100%;
    height:50vh;
    margin-left:3.3%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;

}

.c-li-1 .box{
    width:33.3%;
    height:100%;
    margin:0 auto;
    background: black;
}

.c-li-1 .box:nth-child(3){
    background: transparent;
}

.headline-l{
    display: flex;
    flex-direction: row;
    width:100%;
    height:fit-content;
    align-items: center;
    padding-left:20px;
    font-size: 13px;
    text-align: left;
    color:white;
    font-family: 'Poppins', sans-serif;
    
}

line{
    width:30px;
    height:2px;
    background:red;
    margin-right: 9px;
}

.line3{
    background-color:white ;
}


.headline-t{
    text-align: left;
    width:100%;
    font-size: 30px;
    line-height: 70px;
    letter-spacing: -1px;
    padding:5px 0;
    font-family: 'Poppins', sans-serif;
}

.textline{
    font-size: 14px;
    color:white;
    font-family: 'Poppins', sans-serif;
    margin:0 50px;
}

.c-li-2{
    width:100%;
    height:100vh;
    background: black;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.image-bg{
    width:30%;
    height:100%;
    display: flex;
    flex:none;
    justify-content: center;
    align-items: center;
}

.image{
    width:250px;
    height:450px;
}

.text-box{
    width:70%;
    height:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    color:white;
    font-family: 'Poppins', sans-serif;
    text-align: left;
    font-size: 12px;
    padding:0 40px; 

}
.line4{
    width:20px;
}
.text-box .headline-l{
    padding:0;
    color:red;
}

.headline-t2{
    font-size: 35px;
}

.textline2{
    font-size: 15px;
    margin:15px auto;
}

.btn3-bg{
    margin-top:10px;
}

.btn3{
    width:130px;
    height:30px;
    background:red;
    border:none;
    outline:none;
    border-radius: 3px;
}

.btn3 a{
    color:white;
    text-decoration: none;
    font-family: 'Poppins';
}


@media only screen and (max-width:978px){
    .cont3{
        height:245vh
    }
    .c-li-1{
        flex-direction: column;
        width:100%;
        height:100vh;
        margin:0;
        padding:0;
    }
    .c-li-1 .box{
        width:100%;
    }
    .c-li-1 .box:nth-child(3){
        background:transparent;
    }
    .headline-t{
        font-size: 20px;
        padding:0;
    }
    .textline{
        font-size:11px;
    }
    .c-li-2{
        height:100%;
        flex-direction: column;
    }
    .image-bg{
        height:50%;
        width: 100%;
        padding-top:60px;
    }
    .image{
        width:180px;
        height:332px;
    }
    .text-box{
        width:100%;
        height:50%;
        padding:0 15px 15px;
        padding-top: 60px;
        justify-content: flex-start;
    }
    .textline2{
        font-size: 13px;
    }
}