.side-nav{
    background:black;   
    height:219vh;
    width:55px;
    display: flex;
    flex-direction:column;
    justify-content: flex-start;
    align-items: center;
    position: absolute;
    top:0;
    left:0;
    border-top-right-radius: 5px;
