*{
  padding:0;
  margin:0;
  scroll-behavior: smooth;
}

#loading-bg{
  width:100%;
  height:100vh;
  position:fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background:black;
  z-index: 10;
  display: flex;
  flex:none;
  align-items: center;
  justify-content: center;
}

#loading-p{
  width:70px;
  height:70px;
  background: linear-gradient(45deg, red, transparent 50%, blue );
  border-radius: 50%;
  position: relative;
  animation: loading 1s linear infinite;
}

#loading-p::before{
  content: "";
  position: absolute;
  top:2px;
  bottom:2px;
  left:5px;
  right:5px;
  background: black;
  border-radius: 50%;
}

#loading-p::after{
  content: "";
  position: absolute;
  top:1;
  left:1;
  right:1;
  bottom:1;
  background: linear-gradient(45deg, red, transparent 50%, blue );
  border-radius: 50%;
  filter:blur(30px);
  z-index: -1;
}

t{
  color:white;
  font-size: 24px;
  position: absolute;
  margin-left:15px;
  font-family:'poppins';
}

@keyframes loading{
  from {
    transform: rotate(0deg);
  }
  to{
    transform: rotate(360deg);
  }
}