*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}

.container{
    width:100%;
    height:156vh;
    background: url("../images/landscape.jpg") no-repeat center;
    background-size:370vh 100%;
}


@media only screen and (max-width:978px){
    .container{
        background: url("../images/portrait.jpg");
        background-size:99% 120vh;
        height:200vh;
    }
}