@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');



.container1{
    width:96%;
    height:90vh;
    display: flex;
    margin-left:3.3%;
    overflow: hidden;
}

.part1{
    width:70%;
    height:90vh;
    /* background:white; */
    display:flex;
    flex-direction: column;
    padding-left:59px;
    padding-top:150px;
    color:white;
}

.part2{
    display: none;
}

.part3{
    width:30%;
    height:90vh;
    padding-top:120px;
    padding-right:150px;
}

.linetext{
    display: flex;
    flex-direction: row;
    width:100%;
    height:fit-content;
    align-items: center;
    padding-left:10px;
    font-size: 13px;
    text-align: left;
    color:black;
    font-family: 'Poppins', sans-serif;

}

.line{
    width:30px;
    height:2px;
    background:black;
    margin-right: 9px;
}

.headline{
    text-align: left;
    width:100%;
    font-size: 70px;
    line-height: 70px;
    letter-spacing: -1px;
    padding:5px 0;
    font-family: 'Poppins', sans-serif;
}

.btn-bg1{
    width:100%;
    height:fit-content;
    display:flex;
    flex:none;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top:20px;
}

.btn1{
    width:120px;
    height:5vh;
    background: black;
    color:white;
    border:none;
    outline:none;
    border-radius: 3px;
    font-family: 'Doppio One', sans-serif;
}

@media only screen and (max-width:978px){
    .container1{
        margin:0;
        display: flex;
        flex-direction: column;
    }
    .headline{
        font-size: 35px;
        line-height: 34px;
        font-weight: 100;
        letter-spacing: 1px;
    }
    .part1{
        height:55%;
        width:100%;
        padding:0;
    }

    .part3{
        display: none;
    }

    .part2{
        display: block;
        height:45%;
        width:100%;

    }
    .btn1{
        width:110px;
        height:35px;
    }
    .headline{
        padding:0 25px;
    }

    .btn-bg1{
        padding:10px 25px;
    }
    .linetext{
        padding:0 25px;
    }
    .line{
        width:20px;
    }
    .text{
        font-size: 10px;

    }
}