.ham-navlist{
    position: fixed;
    top:0px;
    left:0;
    height:100vh;
    width: 0;
    background:rgba(0, 0, 0, .9);
    font-family: 'Poppins', sans-serif;
    font-size: 17px;
    z-index: 1;
    text-align: left;
    transition: .5s ease;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
}
.ham-navlist ul{
    padding-top: 50px;
}
.ham-navlist ul li{
    list-style: none;
    letter-spacing: 1px;
    margin: 20px;
    color:white;
}

.ham-navlist ul .slideclose{
    position: absolute;
    top:0;
    right:10px;
    font-size: 20px;
    cursor: pointer;
}

.ham-navlist ul li a:hover{
    color:red;
}

