@import url('https://fonts.googleapis.com/css2?family=Doppio+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Archivo+Black&display=swap');


.navbar{
    display:flex;
    flex-direction: row;
    align-items: center;
    height:8vh;
    width:97%;
    color:white;
    margin-left:40px;

}

.hamenu{
    display: none;
}

.title{
    width:20%;
    height:100%;
    display:flex;
    flex: none;
    align-items: center;
    /* background-color:red; */
    justify-content: flex-star;
    font-size: 23px;
    padding-left:50px;
    font-family: 'Poppins', sans-serif;
    
}

.navlist{
    width:60%;
    height:100%;
    display: flex;
    align-items:center;
    justify-content: center;
}

.navlist ul{
    display:flex;
    flex-direction: row;
    font-family: 'Poppins', sans-serif;
}

.link{
    color:white;
    text-decoration: none;
}

.list-comp{
    list-style:none;
    padding:10px 30px;
    font-size: 12px;
    letter-spacing: 1px;
}
.btn-box{
    width:25%;
    height:100%;
    /* background-color:blue; */
    display:flex;
    justify-content: flex-start;
    align-items: center;
}
.btn{
    background: black;
    width:110px;
    height:35px;
    margin:0 auto;
    border:none;
    font-family: 'Doppio One', sans-serif;
    border-radius:3px;
    letter-spacing: 1px;
    font-size: 11px;
}
.ham-navlist{
    position: fixed;
    top:0px;
    left:0;
    height:100vh;
    width: 0;
    background:rgba(0, 0, 0, .9);
    font-family: 'Poppins', sans-serif;
    font-size: 17px;
    z-index: 1;
    text-align: left;
    transition: .5s ease;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
}
.ham-navlist ul{
    padding-top: 50px;
}
.ham-navlist ul li{
    list-style: none;
    letter-spacing: 1px;
    margin: 20px;
    color:white;
}

.ham-navlist ul .slideclose{
    position: absolute;
    top:0;
    right:10px;
    font-size: 20px;
    cursor: pointer;
}

.ham-navlist ul li a:hover{
    color:red;
}

.social-icon{
    display: flex;
}

.fa-facebook-f{
    color:rgb(255, 23, 23);
    font-size:25px;
}
.fa-twitter{
    color:rgb(255, 23, 23);
    font-size:25px;
}
.fa-instagram{
    color:rgb(255, 23, 23);
    font-size:25px;
}
.fa-github{
    color:rgb(255, 23, 23);
    font-size:25px;
}

@media only screen and (max-width:978px){
    .navlist {  
        display:none;
    }
    .hamenu{
        display: flex;
        padding-left: 20px ;
    }
    .btn-box{
        width:50%;
    }
    .title{
        width:50%;
        font-size: 20px;
        display: flex;
        flex:none;
        padding: 0 10px;
    }
    .btn{
        margin:0;
        width:70px;
        height:30px;
    }
    .btn-box{
        justify-content: flex-end;
        padding-right:0px;
    }
    .side-nav{
        display:none;
    }
    .navbar{
        margin:0;
    }
    /* .ham-navlist{
        position: absolute;
        top:50;
        left:0;
    }
     */
}